.checkbox {
  position: relative;
  --size: 22px;
  --size-inner: 16px;
  --color: var(--black);
}
.checkbox__input {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
}
.checkbox__input:checked + .checkbox__label:after {
  transform: scale(1);
}
.checkbox__input.just-validate-error-field + .checkbox__label::before {
  border-color: var(--red);
}
.checkbox__input.just-validate-error-field + .checkbox__label .checkbox__text {
  color: var(--red);
}
.checkbox__label {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  position: relative;
}
.checkbox__label:before {
  content: "";
  align-self: flex-start;
  flex: 0 0 var(--size);
  width: var(--size);
  height: var(--size);
  border: 1px solid var(--color);
  border-radius: 5px;
  transition: border 0.14s ease-in-out;
  position: relative;
  top: 0px;
}
.checkbox__label::after {
  content: "";
  background-color: var(--color);
  flex: 0 0 var(--size-inner);
  width: var(--size-inner);
  height: var(--size-inner);
  position: absolute;
  top: 3px;
  left: 3px;
  border-radius: 5px;
  transform: scale(0);
  transition: transform 0.14s ease-in-out;
}
.checkbox__text {
  color: var(--color);
  @apply text-[14px] font-medium leading-[1.35] ml-5;
}
.checkbox__text a {
  @apply underline;
}
.checkbox .just-validate-error-label {
  display: none;
}

.checkbox-gray {
  --color: #aaa;
}

