.product-card {
  padding: 32px 32px 0 32px;
  border-radius: 32px;
  background-color: var(--gray);
  min-height: 340px;
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}
.product-card__badge {
  margin-bottom: 12px;
  padding: 0 16px;
  font-size: 16px;
  line-height: 1.375;
  font-weight: 600;
  color: var(--black);
  border-radius: 55px;
  height: 34px;
  backdrop-filter: blur(20px);
  background-color: var(--white);
  display: flex;
  align-items: center;
  align-self: start;
}
.product-card__content {
  margin-top: auto;
}
.product-card__title {
  color: var(--black);
}
.product-card__descr {
  padding-top: 16px;
  font-size: 16px;
  line-height: 1.56;
  color: var(--black);
  font-weight: 500;
  opacity: 0;
  max-height: 32px;
  transition: opacity 0.2s ease-in-out, max-height 0.25s ease-in-out;
}
@media (min-width: 1297px) {
  .product-card__descr::after {
    content: "";
    display: block;
    height: 0;
    transition: height 0.25s ease-in-out;
  }
}
@media (max-width: 1296px) {
  .product-card__descr {
    opacity: 1;
    max-height: 170px;
  }
}
.product-card__arrow {
  position: absolute;
  top: 27px;
  right: 27px;
  width: 25px;
  height: 25px;
  fill: var(--black);
  opacity: 0;
  transform: translate3d(-4px, 4px, 0);
  transition: opacity 0.2s ease-out 0.05s, transform 0.2s ease-out 0.05s;
}
.product-card.product-card--green {
  background: linear-gradient(215deg, #44ca6d 0%, #249e4a 100%);
}
.product-card.product-card--green .product-card__badge {
  background: rgba(255, 255, 255, 0.2);
  color: var(--white);
}
.product-card.product-card--green .product-card__title {
  color: var(--white);
}
.product-card.product-card--green .product-card__descr {
  color: var(--white);
}
.product-card.product-card--green .product-card__arrow {
  fill: var(--white);
}
.product-card.product-card--white {
  background-color: var(--white);
}
.product-card.product-card--white .product-card__badge {
  background-color: var(--gray);
}
@media (min-width: 1297px) {
  .product-card:hover .product-card__descr {
    opacity: 1;
    max-height: 170px;
  }
  .product-card:hover .product-card__descr::after {
    height: 32px;
  }
  .product-card:hover .product-card__arrow {
    opacity: 1;
    transform: translateZ(0);
  }
}

